.App {
    height: 100%;
}

html, body {
    height: 100%;
    margin: 0;
}

#admin-container {

}

#intro {
    font-family: 'Noto Serif', serif;
    font-size: 1rem;
    text-align: center;
    border-style: solid;
    border-radius: 1rem;
    float: right;
    margin-right: 40%;
    margin-top: 10%;
    background: lightgray;
    padding: 1rem;
}

#intro-input {
   height: 10rem;
   width: 10rem;
   overflow-wrap: inherit;
}

#b-update {
   height: 2rem;
   margin: 1rem;
}
#education {
     font-family: 'Noto Serif', serif;
        font-size: 1rem;
        border-style: solid;
        border-radius: 1rem;
        float: right;
        margin-top: 10%;
        background: lightgray;
        padding: 1rem;
        display: inline-block;
        margin-right: 30%;
}

#b-eduction {
    border-style: solid;
    border-radius: 1rem;
    margin-left: 25%;
}
html, body {
height: 100%;
}


#side-menu {
     margin: 0;
     height: 100vh;
     float: left;
     width: 10rem;
     background-color: black;
}

#menu-item {
    font-family: 'Noto Serif', serif;
    background-color: black;
    color: white;
    margin: 0 0 0 0 ;
    height: 10rem;
    padding-left: 2rem;
    padding-top: 2rem;
}
#header-container{
   position: fixed;
   z-index: 100;
   width: 100%;
}

#main-menu {
    width: 100%;
    display: flex;
    flex-direction: row;
    background-color: white;
    margin-bottom: 2rem;
    padding-left: 1rem;
}

#mm-item {
    background-color: white;
    height: 2rem;
    color: black;
    width: 20%;
    margin-top: 1vw;
    font-family: 'Permanent Marker', sans-serif;
    font-size: 2vw;
    padding-bottom: 1.5vw;
    text-align: center;
}

#mm-item:hover{
   color:yellow;
   cursor: pointer;
}

#main-header{
    background: white;
    color: black;
    padding-top: 1vw;
    padding-left: 2vw;
    font-family: 'Permanent Marker', sans-serif;
    font-size: 3vw;
    width: 100%;
    display: flex;
    flex-direction: row;
    cursor: pointer;
}

#main-header:hover{
    color: yellow;
}
#main-intro {
  text-align: center;
  font-family: 'Rajdhani', sans-serif;
  display: flex;
  flex-direction: column;
}

#d-headshot-1 {
  width: 40vw;
  margin: 15vw 30vw 0vw 30vw;
}

#d-headshot-2, #d-headshot-3 {
  margin: 0vw 30vw 0vw 30vw;
  width: 40vw;
}

#d-headshot-4 {
  margin: 0vw 30vw 3vw 30vw;
  width: 40vw;
}

#img-headshot {
  display: block;
  width: 100%;
  height: auto;
  border: 0.2vw solid black;
}

#headshot-cap {
  width: 100%;
  text-align: center;
  margin: 1vw 0vw 1vw 0vw;
  background-color: gray;
  padding-top: 0.5vw;
  padding-bottom: 0.5vw;
  font-size: 1.5vw;
  border-radius: 0.5vw;
}

#main-resume {
    text-align: center;
    font-family: 'Rajdhani', sans-serif;
}
#contact-main {
   text-align: center;
   font-family: 'Rajdhani', sans-serif;
}
#writing-main{
    font-family: 'Rajdhani', sans-serif;
    display: flex;
    flex-direction: column;
    font-size: 1vw;
    padding-top: 2vw;
}

#writing-sample-top {
 margin: 12vw 0 0 5vw;
}

#writing-sample {
 margin: 0 0 0 5vw;
}

#p-sample {
 margin: 0vw 5vw 0vw 2vw;
}

#writing-title {
  font-size: 2vw;
}
#media-main {
    text-align: center;
    font-family: 'Rajdhani', sans-serif;
    display: flex;
    flex-direction: column;
}

#media-top-menu{
 display: flex;
 flex-direction: row;
 height: 2rem;
 position: fixed;
 z-index: 100;
 width: 100%;
}

#media-menu-item {
   margin-top: 10vw;
   width: 50%;
   text-align: center;
   font-family: 'Permanent Marker', sans-serif;
   font-size: 2vw;
   cursor: pointer;
   height: 3vw;
   z-index: 100;
   background-color: white;
   padding-top: 1vw;
}
#media-menu-item:hover{
   font-size: 2.5vw;
}

#media-viewer{
margin-top:12rem;
padding-top: 0;
}
#photos-div {
 display: flex;
 flex-direction: row;
 flex-wrap: wrap;
 width: 50vw;
 margin-top: 2.5rem;
 text-align: center;
 margin-left: 8%;
}

.photo-div {
 text-align: center;
 margin: 1rem;

}

.photo-div:hover {
  cursor: pointer;
}

#display-photo {
    display: flex;
    flex-direction: row;
    margin-top: 1vw;
    text-align: center;
    margin-left: 10%;
    z-index:90;
}

#b-previous {
    margin-right: 3vw;
    margin-top: 15%;
    border:none;
    background-color: white;;
    cursor: pointer;
    height: 5vw;
}

#b-next{
    margin-left: 3vw;
    margin-top: 15%;
    border:none;
    background-color: white;
    cursor: pointer;
    height: 5vw;
}

#b-previous:active, #b-next:active {
    border: none;
}

#gallery-cap {
    margin-top: 2vw;
    width: 50vw;
    text-align: center;
    background-color: white;
    color: black;
    border-radius: 2vw;
    padding: 1vw;
    margin-left: 25%;
    margin-right: 25%;
    font-size: 1.5vw;
}

#main-pic {
    display: block;
    width: 100%;
    height: auto;
}

#main-pic-container {
    width: 40vw;
    margin: 0 2vw 0 2vw;
}

#side-pic{
    display: block;
    width: 100%;
    height: auto;
    margin: 100% 0 0 0;
}

#side-pic-container {
    width: 10vw;
}

